<template>
  <table id="app">
      <th></th>
      <th> </th>
      <th></th>
      <th></th>
      <th></th>
    <tbody v-for="item in items">
      <td>{{item.title}}</td>
      <td>{{item.sub_title}}</td>
      <td>{{item.cost}} </td>
      <td>{{item.price}} </td>
      <td>{{item.quantity}}  </td>
    </tbody>
  </table>
</template>


<script>
export default {
  name: 'app',
  data () {
    return {
      items: []
    }
  },
  beforeMount() {
    var HostUrl = '/api/edit/'+ this.$route.params.id;
    this.$http.get(HostUrl)
      .then(function(response) {
        this.items = response.data;
        console.log(this.items);
      }, function(error) {
        console.log('error in .js:');
        console.log(error);
      }
    );
  }
}
</script>



